<template>
	<view class="box">
		<template v-if="address_allow">
			<view class="top-type">
				<view class="type-l" @click="getLocation">
					<view class="iconfont icondizhidingwei"></view>
					{{address}}
				</view>
				<view class="shop-type">
					<picker v-if="shop_type.length>0" @change="shopChange" :value="shop_index" :range="shop_type" range-key="label">
						<view class="uni-input">{{shopType}}</view>
					</picker>
					<view class="iconfont iconxiala"></view>
				</view>
			</view>
			<mescroll-body ref="mescrollRef" @init="mescrollInit" @down="downCallback" @up="upCallback" :up="upOption">
				<template v-for="(item,k) in data_list">
					<navigator :url="'/pages/goods/shop?merchantShopId='+item.seqId" class="shop-li" :key="k">
						<image lazy-load class="img" :src="item.picUrl" mode="aspectFill"></image>
						<view class="shop-li-r">
							<view class="title">
								<view class="text">
									<view class="shopname">
										{{item.merchantShopName}}
									</view>
									<view class="cardimg">
										<image lazy-load v-if="item.attr1" class="img-card" src="/static/images/diamond_icon.png" mode="aspectFill"></image>
										<image lazy-load v-if="item.attr2" class="img-card" src="/static/images/gold_icon.png" mode="aspectFill"></image>
										<image lazy-load v-if="item.attr3" class="img-card" src="/static/images/silver_icon.png" mode="aspectFill"></image>
									</view>
								</view>
								<template v-if="!item.distanceKm">
									<text class="juli">未知</text>
								</template>
								<template v-else>
									<text class="juli" v-if="item.distanceKm<10">{{item.distanceKm}}KM</text>
									<text class="juli" v-else>10公里外</text>
								</template>

							</view>
							<view class="score parent-score">
								<score :activeScore='3*1' :size="'small'"></score>
							</view>
							<view class="place">地址：{{item.shopAddress}}</view>
							<view class="place">电话：{{item.shopManagerMobile}}</view>
						</view>
					</navigator>
				</template>
			</mescroll-body>
		</template>
		<template v-if="!address_allow&&!lon1">
			<view class="no-address">
				<view class="text">
					无法定位，请授权位置服务
				</view>
				<view class="border-btn" @tap="locationFail('err',2)">
					授权定位
				</view>
			</view>
		</template>
	</view>
</template>

<script>
	import score from '../../components/score.vue'
	import MescrollMixin from "@/components/mescroll-uni/mescroll-mixins.js";
	import MescrollBody from "@/components/mescroll-uni/mescroll-body.vue"
	export default {
		mixins: [MescrollMixin], // 使用mixin
		data() {
			return {
				data_list: [],
				lon1: "1",
				lat1: '',
				address: "正在获取位置...",
				address_allow: false,
				shop_type: [],
				shop_index: 0,
				shopType: ""
			}
		},
		components: {
			score,
			MescrollBody,
		},
		onLoad() {
			this.getNowLocation()
			this.getType();
		},
		methods: {
			upCallback(page) {
				let pageNum = page.num; // 页码, 默认从1开始
				let pageSize = page.size; // 页长, 默认每页10条
				if (!this.lat1) return;
				this.$api.get('/o2oMerchantShop/findNearShopList', {
					params: {
						"lat1": this.lat1,
						"lon1": this.lon1,
						"shopType": this.shopType == "全部" ? "" : this.shopType,
						"pageno": pageNum,
						"pagesize": pageSize
					}
				}).then((res) => {
					if (res.code && res.code != 200) {
						this.mescroll.endErr();
						return
					}
					this.$store.commit('saveAddress', {});
					let data = res;
					// 接口返回的当前页数据列表 (数组)
					let curPageData = data.list;
					// 接口返回的当前页数据长度 (如列表有26个数据,当前页返回8个,则curPageLen=8)
					let curPageLen = curPageData.length;
					// 接口返回的总页数 (如列表有26个数据,每页10条,共3页; 则totalPage=3)
					let totalPage = data.pages;

					//设置列表数据
					if (page.num == 1) this.data_list = []; //如果是第一页需手动置空列表
					this.data_list = this.data_list.concat(curPageData)
					// 请求成功,隐藏加载状态
					//方法一(推荐): 后台接口有返回列表的总页数 totalPage
					this.mescroll.endByPage(curPageLen, totalPage);
				})
			},
			// 选择店铺类型
			shopChange(e) {
				this.shop_index = e.detail.value;
				this.shopType = this.shop_type[e.detail.value].label;
				this.data_list = [];
				this.page_index = 1;
				this.mescroll.resetUpScroll(true)
			},
			// 获取当前地址
			getNowLocation() {
				let self = this;
				uni.getLocation({
					type: 'gcj02',
					success: function(res) {
						self.address_allow = true;
						self.address = '离我最近'
						if (!self.lon1 || !self.lat1) {
							self.lon1 = res.longitude
							self.lat1 = res.latitude;
							self.data_list = [];
							self.page_index = 1;
							self.$nextTick(function() {
								self.mescroll.resetUpScroll(true)
							})
						}
					},
					fail: function(errMsg) {
						self.locationFail(errMsg)
					},
				});
			},
			// 获取位置权限 time 未获取到位置信息再次点击获取
			locationFail(err, time = 1) {
				let self = this;
				if (err.errMsg === "getLocation:fail auth deny" || err.errMsg === "getLocation:fail authorize no response" || time ==
					2) { // 没有授权，重新授权，
					uni.showModal({
						title: '授权提示',
						content: "是否允许开启位置信息",
						success: (res) => {
							if (res.confirm) { // 点击确定，则调用位置授权
								uni.openSetting({
									success(settingdata) {
										self.address_allow = true;
										if (settingdata.authSetting["scope.userLocation"]) {
											uni.showToast({
												title: '获取权限成功'
											});
											console.log('获取权限成功');
											self.getNowLocation();
										} else {
											self.address_allow = false;
											self.lon1 = "";
											self.address = '获取失败';
											console.log("获取权限失败")
										}
									}
								})
							}
							if (res.cancel) {
								self.address_allow = false;
								self.lon1 = "";
								self.address = '获取失败';
								console.log("取消获取权限")
							}
						}
					})
				}
			},
			// 调转到地图
			getLocation() {
				uni.chooseLocation({
					latitude: this.lat1,
					longitude: this.lon1,
					success: (res) => {
						console.log(res);
						this.address = res.name
						this.lon1 = res.longitude
						this.lat1 = res.latitude;
						this.mescroll.resetUpScroll(true)
					}
				})
			},
			getType() {
				this.$api.get('/sysDict/list', {
					params: {
						type: 'SHOPTYPE',
						pageno: 1,
						pagesize: 100
					}
				}).then(res => {
					if (res.code && res.code != 200) {
						return
					};
					let all = {
						createBy: null,
						createDate: null,
						description: "全部",
						label: "全部",
						parentId: null,
						parentType: null,
						parentValue: null,
						remarks: "",
						seqId: "-1",
						sort: 0,
						systemCode: null,
						type: "ALL",
						updateBy: null,
						updateDate: null,
						value: "0"
					}
					this.shop_type = res.list;
					this.shop_type.unshift(all)
					if (!this.shopType) {
						this.shopType = this.shop_type[0].label
					}
				})
			},
		}
	}
</script>

<style lang="scss">
	.no-address {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		justify-content: center;
		padding-top: 16vh;

		.text {
			color: #666;
			font-size: 30rpx;
			width: 100%;
			text-align: center;
			margin-bottom: 30rpx;
		}

	}


	.top-type {
		width: 682rpx;
		height: 100rpx;
		display: flex;
		justify-content: space-between;
		align-items: center;
		font-size: 26rpx;
		margin: 0 auto;

		.type-l {
			display: flex;
			font-size: 26rpx;

			.iconfont {
				color: #FF9E97;
				margin-right: 6rpx;
			}
		}

		.shop-type {
			display: flex;
			justify-content: center;
			align-items: center;
			width: 150rpx;
			height: 38rpx;
			background: rgba(255, 255, 255, 1);
			box-shadow: 0px 4rpx 8rpx 0px rgba(0, 0, 0, 0.16);
			border-radius: 19rpx;
			font-size: 24rpx;

			.iconfont {
				font-size: 20rpx;
				margin-left: 6rpx;
			}
		}
	}

	.shop-li {
		width: 622rpx;
		height: 140rpx;
		background: rgba(255, 255, 255, 1);
		box-shadow: 0px 0px 10rpx 0px rgba(0, 0, 0, 0.1);
		border-radius: 10rpx;
		display: flex;
		justify-content: space-between;
		padding: 30rpx 30rpx;
		margin: 30rpx auto;
		font-size: 20rpx;

		.img {
			width: 168rpx;
			height: 140rpx;
			border-radius: 4rpx;
			margin-right: 30rpx;
			flex-shrink: 0;
			background: $color-primary;
		}

		.shop-li-r {
			flex-grow: 1;
			width: calc(100% - 220rpx);

			.title {
				width: 100%;
				display: flex;
				justify-content: space-between;
				align-items: center;
				margin-top: -10rpx;

				.text {
					width: 320rpx;
					font-size: 30rpx;
					display: flex;

					.shopname {
						max-width: 210rpx;
						overflow: hidden;
						text-overflow: ellipsis;
						white-space: nowrap;
						margin-right: 10rpx
					}

					.cardimg {
						width: 90rpx;
						flex-shrink: 0;
					}


					.img-card {
						width: 20rpx;
						height: 20rpx;
						margin-right: 10rpx;
					}
				}

				.juli {
					width: 100rpx;
					text-align: right;
					flex-shrink: 0;
					color: #FF3D34;
				}
			}

			.score {
				width: 164rpx;
				// #ifdef H5
				width: 180rpx;
				// #endif
			}

			.parent-score {
				margin: 16rpx 0;
			}

			.place {
				width: 100%;
				color: #666666;
				overflow: hidden;
				text-overflow: ellipsis;
				white-space: nowrap;
				font-size: 20rpx;
			}
		}
	}

	.shop-li {
		margin-top: 0;
	}
</style>
